'use strict';

/**
* Binomial distributed pseudorandom numbers.
*
* @module @stdlib/random-base-binomial
*
* @example
* import binomial from '@stdlib/random-base-binomial';
*
* var v = binomial( 20, 0.4 );
* // returns <number>
*
* @example
* import { factory as factory } from '@stdlib/random-base-binomial';
*
* var binomial = factory( 90, 0.8, {
*     'seed': 297
* });
*
* var v = binomial();
* // returns <number>
*/

// MODULES //

import setReadOnly from '@stdlib/utils-define-nonenumerable-read-only-property';
import binomial from './main.js';
import factory from './factory.js';


// MAIN //

setReadOnly( binomial, 'factory', factory );


// EXPORTS //

export default binomial;
