'use strict';

/**
* Normally distributed pseudorandom numbers using the Box-Muller transform.
*
* @module @stdlib/random-base-box-muller
*
* @example
* import randn from '@stdlib/random-base-box-muller';
*
* var r = randn();
* // returns <number>
*
* @example
* import { factory as factory } from '@stdlib/random-base-box-muller';
*
* var randn = factory({
*     'seed': 12345
* });
*
* var r = randn();
* // returns <number>
*/

// MODULES //

import setReadOnly from '@stdlib/utils-define-nonenumerable-read-only-property';
import randn from './main.js';
import factory from './factory.js';


// MAIN //

setReadOnly( randn, 'factory', factory );


// EXPORTS //

export default randn;
