'use strict';

/**
* Negative binomial distributed pseudorandom numbers.
*
* @module @stdlib/random-base-negative-binomial
*
* @example
* import negativeBinomial from '@stdlib/random-base-negative-binomial';
*
* var v = negativeBinomial( 20, 0.5 );
* // returns <number>
*
* @example
* import { factory as factory } from '@stdlib/random-base-negative-binomial';
* var negativeBinomial = factory( 10, 0.3, {
*     'seed': 297
* });
*
* var v = negativeBinomial();
* // returns <number>
*
* @example
* import { factory as factory } from '@stdlib/random-base-negative-binomial';
* var negativeBinomial = factory({
*     'seed': 297
* });
*
* var v = negativeBinomial( 10, 0.67 );
* // returns <number>
*/

// MODULES //

import setReadOnly from '@stdlib/utils-define-nonenumerable-read-only-property';
import negativeBinomial from './main.js';
import factory from './factory.js';


// MAIN //

setReadOnly( negativeBinomial, 'factory', factory );


// EXPORTS //

export default negativeBinomial;
